export default {
  name: "UniPopup",
  props: {
    // 开启动画
    animation: {
      type: Boolean,
      default: true
    },
    // 弹出层类型，可选值，top: 顶部弹出层；bottom：底部弹出层；center：全屏弹出层
    type: {
      type: String,
      default: "center"
    },
    // 是否开启自定义
    custom: {
      type: Boolean,
      default: false
    },
    // maskClick
    maskClick: {
      type: Boolean,
      default: true
    },
    show: {
      type: Boolean,
      default: true
    }
  },
  data() {
    return {
      ani: "",
      showPopup: false
    };
  },
  watch: {
    show(newValue) {
      if (newValue) {
        this.open();
      } else {
        this.close();
      }
    }
  },
  created() {},
  methods: {
    clear() {},
    open() {
      this.$emit("change", {
        show: true
      });
      this.showPopup = true;
      this.$nextTick(() => {
        setTimeout(() => {
          this.ani = "uni-" + this.type;
        }, 30);
      });
    },
    close(type) {
      if (!this.maskClick && type) return;
      this.$emit("change", {
        show: false
      });
      this.ani = "";
      this.$nextTick(() => {
        setTimeout(() => {
          this.showPopup = false;
        }, 300);
      });
    }
  }
};
